@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
:root {
  --main-bg-color: hsl(0, 0%, 15%);
  --main-text-color: hsl(0, 0%, 30%);
  --accent: hsl(25, 100%, 50%);
  --accent-light: hsl(25, 100%, 90%);
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  color: hsl(0, 0%, 30%);
  color: var(--main-text-color);
  background-color: hsl(0, 0%, 15%);
  background-color: var(--main-bg-color);;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input, button {
  border-radius: 5px;
  padding: 0.5em;
}

select {
  border-radius: 5px;
  margin-left: 0.5em;
  color: hsl(0, 0%, 30%);
  color: var(--main-text-color);
}

button {
  cursor: pointer;
  color: hsl(0, 0%, 30%);
  color: var(--main-text-color);
  font-weight: 700;
  transition: 0.5s;
}

button:hover {
  background-color: hsl(25, 100%, 50%);
  background-color: var(--accent);
  color: white;
}

.border-orange {
  border-top: 7px solid hsl(25, 100%, 50%);
  border-top: 7px solid var(--accent);
  border-bottom: 7px solid hsl(25, 100%, 50%);
  border-bottom: 7px solid var(--accent);
}

.login {
  display: grid;
  place-items: center;
  height: 100vh;
}

.login-form {
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  padding: 5rem;
  border-radius: 5px;
}

.login-form > label {
  margin-bottom: 1em;
}

input {
  width: 100%;
  margin-top: 0.5em;
}
.expense-form {
  display: flex;
  grid-gap: 3em;
  gap: 3em;
}

.expense {
  max-width: 300px;
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
table {
  width: 100%;
  margin-top: 1rem;
  font-size: 0.8rem;
  line-height: 1.2;
  text-align: center;
}

thead {
  background-color: var(--accent);
  color: white;
  margin-bottom: 1em;
}

th {
  padding: 0.5em 1em;
  border-radius: 5px;
}

tr:nth-child(even) {
  background-color: hsl(0, 0%, 90%);
}

tr:hover {
  background-color: var(--accent-light);
}

.table-button {
  margin: 0.25em 0;
}
header {
  display: flex;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;
}

.header-expense {
  display: flex;
  grid-gap: 1em;
  gap: 1em;
}

.email::before, .total::before, .currency::before {
  font-weight: 700;
}

.email::before {
  content: 'Email: ';
}

.total::before {
  content: 'Total: ';
}

.currency::before {
  content: 'Moeda: ';
}
.wallet {
  background-color: whitesmoke;
  width: 1100px;
  padding: 2rem;
  margin: 5rem auto;
  border-radius: 5px;
}


